import Api from '@/server/api'

const state = {
	products: [],
};

const actions = {
	getProducts({
		commit
	}) {
		// return new Promise((resolve, reject) => {
		Api().get('/product').then(res => {
			// resolve(res.data.product)
			commit('SET_PRODUCTS', res.data.product)
		}).catch(err => {
			// reject(err);
			console.log(err);
		})
		// })
	},
	addProduct({
		commit
	}, product) {
		return new Promise((resolve, reject) => {
			Api()
				.post("http://localhost:3000/api/product/", product)
				.then((res) => {
					resolve(res.data.message);
					commit('ADD_PRODUCT', res.data.product)
				})
				.catch((err) => {
					reject(err)
				});
		})
	}
};


const mutations = {
	SET_PRODUCTS(state, payload) {
		if (payload) {
			state.products = payload
		} else {
			state.products = []
		}

	},
	ADD_PRODUCT(state, payload) {
		state.products.push(payload)
	}
};

export const product = {
	namespaced: true,
	state,
	actions,
	mutations
}