import Api from '@/server/api';


const state = {
	user: {
		username: '',
		password: ''
	},
	token: null,
	message: ''
};

const actions = {
	login({
		commit
	}, data) {
		let username = data.username;
		let password = data.password;
		return new Promise((resolve, rejects) => {
			Api().post('/customer/login', {
				username,
				password
			}).then(res => {
				resolve(res.headers.authorization);
				commit('SET_TOKEN_LOGIN', res.headers.authorization)
			}).catch(err => {
				rejects(err);
				commit('SET_EROOR', err)
			})
		})
	},
	logout({
		commit
	}) {
		commit('LOG_OUT')
	}
};

const mutations = {
	SET_TOKEN_LOGIN(state, payload) {
		state.token = payload;
		console.log(state.token)
	},
	SET_EROOR(state, message) {
		state.message = message
	},
	LOG_OUT(state) {
		state.token = null
	}
};

export const user = {
	state,
	actions,
	mutations
}