import Api from '@/server/api';

const state = {
	category: [],
};

const actions = {
	async getListCategory({
		commit
	}) {
		await Api().get('/category').then(res => {
			commit('SET_LIST_CATEGORY', res.data);
		})
	},
	async updateStatus({
		commit
	}, category) {
		if (category.status === true) {
			await Api().patch(`/category/${category.id}`, [{
				propName: "status",
				value: "false"
			}, ]).then(() => {
				commit('UPDATE_STATUS_CATEGORY', category.id)
			});
		} else {
			await Api().patch(`/category/${category.id}`, [{
				propName: "status",
				value: "true"
			}, ]).then(() => {
				commit('UPDATE_STATUS_CATEGORY', category.id)
			});
		}
	},
	async addCategory({
		commit
	}, category) {
		if (category.status === 'Active') {
			category.status = true
			await Api().post('/category', category).then((res) => {
				if (res.data.success == false) {
					console.log('Category exites');
					commit('ADD_CATEGORY', res.data.success)
				} else {
					commit('ADD_CATEGORY', res.data.category)
				}
			}).catch(err => {
				console.log(err);
			})
		} else {
			category.status = false
			await Api().post('/category', category).then((res) => {
				commit('ADD_CATEGORY', res.data.category)
			}).catch(err => {
				console.log(err);
			})
		}
	},
	async deleteCategory({
		commit
	}, categoryId) {
		await Api().delete(`/category/${categoryId}`).then(() => {
			commit('DELETE_CATEGORY', categoryId)
		})
	},
	async doUpdateCategory({
		commit
	}, category) {
		const id = category[0].categoryId;
		await Api().patch(`/category/${id}`, category)
		commit('UPDATE_CATEGORY', category)
	}
};

const mutations = {
	SET_LIST_CATEGORY(state, payload) {
		if (payload) {
			state.category = payload
		} else {
			state.category = [];
		}
	},
	ADD_CATEGORY(state, payload) {
		if (payload) {
			state.category.push(payload);
		}
	},
	UPDATE_STATUS_CATEGORY(state, payload) {
		const data = state.category.find(item => {
			return item.id == payload
		});
		if (data.status === true) {
			data.status = false
		} else {
			data.status = true
		}
	},
	DELETE_CATEGORY(state, payload) {
		let categorys = state.category.filter(cate => cate.id !== payload)
		state.category = categorys
	},
	UPDATE_CATEGORY(state, payload) {
		const data = state.category.find(item => {
			return item.id === payload[0].categoryId
		})
		data.name = payload[1].value;
		data.description = payload[2].value;
		data.slug = payload[3].value;
	}
};

const getters = {
	getCategory: state => {
		return state.category
	}
}


export const category = {
	namespaced: true,
	getters,
	state,
	actions,
	mutations
}