            <v-icon>{{ link.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ link.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <!--End list Item -->

      <!--- List group -->

      <!-- <v-list
        active-class="blue white--text"
        nav
        v-for="link in links"
        :key="link.text"
        dark
      >
        <v-list-group :prepend-icon="link.icon" value="true">
          <template v-slot:activator>
            <v-list-item-title v-text="link.text"></v-list-item-title>
          </template>

          <v-list-item
            v-for="item in link.list"
            :key="item.text"
            route
            :to="item.route"
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-title v-text="item.text"></v-list-item-title>
          </v-list-item>
        </v-list-group>
