<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      drawer: true,
      mini: true,
      links: [
        {
          icon: "dashboard",
          text: "Dashboard",
          route: "/home",
        },
        {
          icon: "category",
          text: "Category",
          route: "/category",
        },
        {
          icon: "list",
          text: "Product",
          route: "/product",
        },
        {
          icon: "money",
          text: "Variation",
          route: "/variation",
        },
      ],
      items: [
        { title: "Profile", route: "/profile" },
        { title: "Setting", route: "/setting" },
      ],
    };
  },
  methods: {
    ...mapActions({ logout: "logout" }),
    doLogout() {
      this.logout();
      localStorage.removeItem("token");
      this.$router.push("/login");
    },
  },
};
</script>

